.card{
    width: 30%;
}
.icon-card-item{
    background-color: rgba(73, 73, 73, 0.7);
    padding: 6px
}
.card-head .heading-toggler{
    opacity: 0;
    transition: 0.5s all ease-in-out;
}
.card-head:hover>.heading-toggler{
    opacity: 1;
    transition: 0.5s all ease-in-out;
}
.card{
    box-shadow: 0px 0px 3px 3px rgba(0,0,0,.1);
    transition: 0.5s all ease-in-out;
    border-radius: 6px;
}
.card:hover{
    box-shadow: 0px 0px 6px 6px rgba(0,0,0,.3);
    transition: 0.5s all ease-in-out;
}
@media(max-width:991px){
    .card{
        width: 48%;
    }
}
@media(max-width:600px){
    .card{
        width: 100%;
    }
}