.overly{
    position: absolute;
    top:0px;
    width:100%;
    background-color: rgba(42, 42, 94, 0.6);
    height: 100%;
}
.form-item{
    display: flex;
    flex-direction: row;
    background-color: white;
    padding: 28px;
    justify-content: space-between;
    align-items: center;
}
.form-item span{
    color: black;
}
.form-item .hero-icon{
    color: black;
    font-size: 20px;
}
.hero-feature{
    box-shadow: 2px 2px 3px 3px rgba(0,0,0,.2);
    transition: 0.5s all ease-in-out;
}
.hero-feature:hover{
    background-color: red;
    color: white !important;
    transition: 0.5s all ease-in-out;
}
.hero-feature:hover>.hero-feature-icon{
   color: white;
   transition: 0.5s all ease-in-out;
}
.hero-features{
    position: absolute;
    bottom: -22%;
    width: 100%;
}
@media(min-width:991px){
    .hero-feature{
        width: 18% !important;
    }
}
@media(max-width:991px){
    .form-item{
        width: 100%;
        border-radius: 6px;
        margin-bottom: 10px;
    }
    .hero-btn{
        width: 30%;
        margin-top: 10px;
        border-radius: 6px;

    }
    .hero-features{
        position: absolute;
        bottom: -35%;
        width: 100%;
    }
    .hero-feature{
        width: 30% !important;
    }
}
@media(max-width:500px){
    .hero-title{
        font-size: 30px !important;
        line-height: 45px !important;
    }
}