.article{
    width: 30%;
    box-shadow: 0px 0px 4px 3px rgba(0,0,0,.1);
    transition: 0.5s all ease-in-out;
}
.article:hover{
    box-shadow: 0px 0px 6px 6px rgba(0,0,0,.3);
    transition: 0.5s all ease-in-out;
}
.article:hover>.article-body>.article-title{
    transition: 0.5s all ease-in-out;
    color: red;
}
@media(max-width:1023px){
    .article{
        width:48% !important;
    }
}
@media(max-width:991px){
    .article{
        width:48% !important;
    }
}
@media(max-width:768px){
    .article{
        width: 100% !important;
    }
}