.form-input{
    width: 52%;
    margin: auto;
}
.input-email:hover{
    box-shadow: 0px 0px 4px 4px rgba(0,0,0,.3);
    transition: 0.5s all ease-in-out;
}
.input-email{
    width: 70%;
    height: 64px;
    border: none;
    text-indent: 20px;
    margin-right: 10px;
    transition: 0.5s all ease-in-out;
}
.input-email:focus{
    outline: none;
}
.contact{
    background-color: rgba(0,0,0,.08);
}
@media(max-width:1024px){
    .form-input{
        width:80%
    }
}
@media(max-width:991px){
    .form-input{
        width:80%
    }
}
@media(max-width:768px){
    .form-input{
        width: 80%;
    }
}
@media(max-width:616px){
    .form-input{ 
        width: 90%;
    }
    .input-email{
        width: 90%;
        margin: auto;
    }
    .input-sub{
        margin: auto;
        margin-top: 20px;
    }
}