.nav-item > .active {
    color:rgba(255, 0, 0, 0.795)
}
.links-nav .header li{
    cursor: pointer;
}
.toggler-icon{
    width: 30px;
    height: 30px;
    padding:2px;
    display: none;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    cursor: pointer;
}
.links-nav .header{
  z-index: 30 !important;
  background-color: white;
}
.toggler-icon span{
    width: 25px;
    height: 3px;
    background-color: rgba(255, 0, 0, 0.795);
}
@media(min-width:992px){
  .header{
        display: flex !important;
        opacity: 1 !important;
        flex-direction: row !important;
    }
}
@media(max-width:991px){
    .toggler-icon{
        display: flex;
    }
    .header{
        position: absolute;
        top: 100px;
        left: 0px;
        width: 100%;
        z-index: 1;
        box-shadow: 3px 3px 4px 1px rgba(117, 117, 117, 0.4);
    }
    .links-nav .header li{
        margin-right: 0px;
    }
}
@media(max-width:500px){
    .header {
        top:130px
    }
}