.howItWOrk-part{
    box-shadow: 0px 0px 2px 2px rgba(0,0,0,.1);
}
.bord{
    border: 1px solid var(--color-second);
    border-radius: 4px;
}
.howItWOrk-part{
    transition: 0.5s all ease-in-out;
}
.howItWOrk-part:hover{
    background-color: red;
    transition: 0.5s all ease-in-out;
}
.howItWOrk-part:hover>.part-icon{
    background-color: white;
    transition: 0.5s all ease-in-out;
}
.howItWOrk-part:hover>.part-icon>.how-icon{
    color: red;
    transition: 0.5s all ease-in-out;
}
.header{
    margin: auto;
    text-align: center;
}
.howItWOrk-part:hover>h2{
    color: white;
    transition: 0.5s all ease-in-out;
}
.howItWOrk-part:hover>p{
    color: white;
    transition: 0.5s all ease-in-out;
}
.howItWOrk-part:hover>.link-div>a{
 color: white;
 transition: 0.5s all ease-in-out;
}